    background: url("//cdn.cnbj1.fds.api.mi-img.com/mbox/img/top-leaf1.png")
      center center no-repeat;
    background-size: cover;
  }
  #top-leaf6 {
    will-change: transform;
    transition: transform 4.5s ease-out, opacity 4.5s ease-out;
    position: absolute;
    width: getRem(718);
    height: getRem(566);
    opacity: 0.5;
    transform: rotate(-1deg) scale(1.2);
    left: -6rem;
    bottom: getRem(1700);
    background: url("//cdn.cnbj1.fds.api.mi-img.com/mbox/img/top-leaf5.png")
      center center no-repeat;
    background-size: cover;
  }
  #work-intro {
    position: absolute;
    bottom: 3.4rem;
    text-align: right;
    right: 1rem;
    .title {
      font-size: 0.6rem;
      margin-bottom: 0.3rem;
      color: #7d839e;
    }
    .subtitle {
      display: flex;
      height: getRem(60);
      font-size: 0.4rem;
      align-items: center;
      color: #7d839e;
      img {
        width: getRem(60);
        height: getRem(60);
        border-radius: 50%;
        border: none;
        margin: 0 0.3rem 0 0.3rem;
      }
    }
    #edit-work {
      position: absolute;
      width: getRem(28);
      height: getRem(28);
      right: getRem(-34);
      top: getRem(18);
      background: url("../assets/viewer/edit.svg") center center no-repeat;
      background-size: cover;
    }
  }
  #control-panal {
    position: absolute;
    width: 100%;
    bottom: 0rem;
    display: flex;
    height: 2.4rem;
    padding: 0 getRem(50);
    justify-content: space-between;
    align-items: center;
    .purchaseBtn {
      position: relative;
      width: getRem(250);
      height: getRem(92);
      border: none;
      border-radius: getRem(46);
      background-color: #4266f5;
      font-size: 0.47rem;
      color: white;
      text-align: center;
      line-height: getRem(92);
    }
    .makeBtn {
      position: relative;
      width: getRem(250);
      height: getRem(92);
      border: none;
      border-radius: getRem(46);
      background-color: #4266f5;
      font-size: 0.47rem;
      color: white;
      text-align: center;
      line-height: getRem(92);
    }
  }
  #playBtn {
    width: 2rem;
    height: 2rem;
    background-color: pink;
  }
}
.loading {
  #spirit {
    transition: none;
    transform: translateY(13rem) scale(0.85);
  }
  #mb {
    transition: none;
    transform: translateY(13rem);
  }
  #ground {
    transition: none;
    transform: translateY(13rem);
  }
  #low-leaf1 {
    transition: none;
    transform: translateY(13rem);
    opacity: 1;
  }
  #low-leaf2 {
    transition: none;
    transform: translateY(13rem) translateX(2rem);
  }
  #low-leaf3 {
    transition: none;
    transform: translateY(13rem);
  }
  #low-leaf4 {
    transition: none;
    transform: translateY(13rem) rotate(0deg);
  }
  #mid-leaf1 {
    transition: none;
    transform: translateY(12rem) rotate(9deg) scale(0.9);
    opacity: 0.9;
  }
  #mid-leaf2 {
    transition: none;
    transform: translateY(14rem) translateX(1.8rem) rotate(-8deg) scale(0.9);
    opacity: 0.8;
  }
  #mid-leaf3 {
    transition: none;
    transform: translateY(14rem) rotate(8deg) scale(0.9);
    opacity: 0.8;
  }
  #mid-leaf4 {
    transition: none;
    transform: translateY(14rem) rotate(0deg) scale(0.9);
    opacity: 0.8;
  }
  #top-leaf1 {
    transition: none;
    transform: translateY(16.5rem) rotate(35deg) scale(0.7);
    opacity: 1;
  }
  #top-leaf2 {
    transition: none;
    transform: translateY(14.5rem) translateX(-1rem) rotate(-20deg) scale(0.6);
    opacity: 0.9;
  }
  #top-leaf3 {
    transition: none;
    transform: translateY(15rem) translateX(1rem) rotate(13deg) scale(0.6);
    opacity: 0.95;
  }
  #top-leaf4 {
    transition: none;
    transform: translateY(13rem) translateX(4.5rem) rotate(52deg) scale(0.7);
    opacity: 1;
  }
  #top-leaf5 {
    transition: none;
    transform: translateY(18rem) translateX(-5rem) rotate(12deg) scale(0.7);
    opacity: 0.3;
  }
  #top-leaf6 {
    transition: none;
    transform: translateY(14.5rem) translateX(2.5rem) rotate(39deg) scale(1);
    opacity: 0.5;
  }
}
.bounce-enter-active {
  transition: all 2s cubic-bezier(0.19, 2, 0.32, 0.91);
}
.bounce-leave-active {
  transition: all 0.8s cubic-bezier(0.19, 1.58, 0.42, 0.91);
}
.bounce-enter, .bounce-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateY(3rem);
  opacity: 0;
}
.fade-enter-active {
  transition: all 0.3s ease-out;
}
.fade-leave-active {
  transition: all 0.3s ease-out;
}
.fade-enter, .fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
@keyframes music-aurora {
  0% {
    background-position: 0 0;
  }
  // 50% {
  //   background-position: 0 getRem(-4039); // background-position: 0 -97.7%
  //   // background-position: 0 getRem(-1590); // background-position: 0 -97.7%
  // }
  100% {
    background-position: 0 getRem(-4039); // background-position: 0 -97.7%
  }
}
@keyframes box-shine {
  0% {
    box-shadow: 0px 0px 0px 0px rgba(252, 245, 141, 1);
  }
  50% {
    box-shadow: 0px 0px 31px 0px rgba(252, 247, 170, 0.8);
  }
  100% {
    box-shadow: 0px 0px 0px 0px rgba(252, 245, 141, 1);
  }
}
#title-update-mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  .mb-dialog {
    position: relative;
    width: getRem(570);
    padding-top: getRem(75);
    border-radius: getRem(50);
    background-color: rgba(255, 255, 255, 0.96);
    display: flex;
    flex-direction: column;
    .dialog-sector {
      display: flex;
      justify-content: space-between;
    }
    .title {
      flex: 1;
      padding: 0 getRem(77);
      display: flex;
      align-items: flex-end;
      position: relative;
      width: 100%;
      font-size: 0.32rem;
    }
    .input {
      flex: 2;
      flex-direction: column;
      padding: 0 getRem(77);
      display: flex;
      align-items: flex-start;
      justify-content: center;
      position: relative;
      width: 100%;
      text-align: center;
      font-size: 0.5rem;
      input {
        background: transparent;
        border: none;
        outline: none;
        width: 100%;
        height: 1rem;
        font-size: 0.54rem;
      }
      .splitter {
        position: relative;
        width: 100%;
        height: 1px;
        background-color: gray;
        padding: 0 getRem(77);
      }
    }
    .btns {
      flex: 4;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      width: 100%;
      text-align: center;
      font-size: 0.5rem;
      span.btn {
        position: relative;
        width: 100%;
        height: 100%;
        flex: 1;
        display: flex;
        align-items: center;
        padding-top: 0.6rem;
      }
      .cancel {
        color: gray;
        text-align: right;
        justify-content: flex-start;
        padding-left: getRem(77);
      }
      .confirm {
        color: blue;
        text-align: left;
        justify-content: flex-end;
        padding-right: getRem(77);
      }
    }
  }
}
</style>



